import { Component, OnInit, OnDestroy, effect, Injector } from '@angular/core';
import { UserPreferencesService } from '../../services/user-preferences.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-default',
  templateUrl: './default.component.html',
  styleUrls: ['./default.component.scss']
})
export class DefaultComponent implements OnInit, OnDestroy {
  showAccessRequest = false;
  private destroy$ = new Subject<void>();

  // Use field initializer for effect - this runs in injection context
  private preferencesEffect = effect(() => {
    const loaded = this.userPreferences.preferencesLoaded();
    if (loaded) {
      this.checkTabAccess();
    }
  });

  constructor(
    public userPreferences: UserPreferencesService,
    private injector: Injector
  ) {}

  ngOnInit(): void {
    // Initial check in case preferences are already loaded
    if (this.userPreferences.preferencesLoaded()) {
      this.checkTabAccess();
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  private checkTabAccess(): void {
    const userData = this.userPreferences.getUserData();
    if (userData?.preferences?.displaySettings) {
      const enabledTabs = userData.preferences.displaySettings['enabledTabs'] || [];
      // If no tabs are enabled, show access request
      if (enabledTabs.length === 0) {
        this.showAccessRequest = true;
      }
    }
  }
} 